import React, { Component } from 'react';
import './Calculator.css';

class Calculator extends Component {
    constructor() {
        super()

        this.state = {
            total: 0,
            operation: '',
            currentNum: '',
            
        }

        this.addition = this.addition.bind(this);
        this.subtraction = this.subtraction.bind(this);
        this.multiplication = this.multiplication.bind(this);
        this.division = this.division.bind(this);
        this.handleCurrentNum = this.handleCurrentNum.bind(this);
        this.backspace = this.backspace.bind(this);
        this.clear = this.clear.bind(this);
    }

    addition() {

    }

    subtraction() {

    }

    multiplication() {

    }

    division() {

    }

    handleCurrentNum(e) {
        this.setState({ currentNum: this.state.currentNum + e.target.value });
        console.log(this.state.currentNum);
    }

    backspace() {
        this.setState({ currentNum: this.state.currentNum.substring(0, this.state.currentNum.length-1) });
    }

    clear() {
        this.setState({ currentNum: '' })
    }

    render() {
        return (
            <div>
                <div className="solution">
                    <p>{this.state.currentNum}</p>
                </div>
                <div className="calc-container">
                    <div className="numbers">
                        <div className="calc-row">
                            <button className="calc-num" type="button" value="7" onClick={this.handleCurrentNum}>7</button>
                            <button className="calc-num" type="button" value="8" onClick={this.handleCurrentNum}>8</button>
                            <button className="calc-num" type="button" value="9" onClick={this.handleCurrentNum}>9</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-num" type="button" value="4" onClick={this.handleCurrentNum}>4</button>
                            <button className="calc-num" type="button" value="5" onClick={this.handleCurrentNum}>5</button>
                            <button className="calc-num" type="button" value="6" onClick={this.handleCurrentNum}>6</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-num" type="button" value="1" onClick={this.handleCurrentNum}>1</button>
                            <button className="calc-num" type="button" value="2" onClick={this.handleCurrentNum}>2</button>
                            <button className="calc-num" type="button" value="3" onClick={this.handleCurrentNum}>3</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-num" type="button" value="0" onClick={this.handleCurrentNum}>0</button>
                            <button className="calc-num" type="button" value="." onClick={this.handleCurrentNum}>.</button>
                        </div>
                    </div> 
                    <div className="operations">
                        <div className="calc-row">
                            <button className="calc-operation" type="button" value="division">÷</button>
                            <button className="calc-operation" type="button" value="backspace" onClick={this.backspace}>⌫</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-operation" type="button" value="miltiplication">x</button>
                            <button className="calc-operation" type="button" value="all clear">AC</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-operation" type="button" value="subtraction">-</button>
                            <button className="calc-operation" type="button" value="clear" onClick={this.clear}>C</button>
                        </div>
                        <div className="calc-row">
                            <button className="calc-operation" type="button" value="addition">+</button>
                            <button className="calc-operation" type="button" value="equals">=</button>
                        </div>
                    </div>
                    <div className="calc-solution">

                    </div>
                </div>
            </div>
        )
    }
}

export default Calculator;


